<template>
  <div>
    <el-tabs
      v-model="activename"
      @tab-click="handleClick"
      tab-position="left"
    >
      <el-tab-pane
        label=""
        name="status"
      >
        <div>
          <p></p>
          <p>1.15%</p>
          <p>
            2.30%
          </p>
          <p>
            3.20%
          </p>
          <p>
            4.15%
          </p>
          <el-table
            :data="statusTabledata"
            border
            style="width: 100%"
          >
            <el-table-column
              prop="name"
              width="250"
              label=""
            >
              <div slot-scope="scope">
                <router-link to>
                  {{ scope.row.name }}
                </router-link>
              </div>
            </el-table-column>

            <el-table-column
              width="180"
              prop="type"
              label=""
            />
            <el-table-column
              width="180"
              prop="status"
              label=""
            />
            <el-table-column
              prop="tips"
              label=""
            />
          </el-table>
        </div>
      </el-tab-pane>
      <!-- / -->
      <el-tab-pane
        ref="addCourseref"
        name="addCourse"
        v-if="role != 3"
      >
        <span slot="label">{{ addCourselabel }}</span>
        <el-form
          label-width="90px"
          :rules="rules"
          :inline="true"
          ref="courseFormref"
          :model="courseForm"
        >
          <el-form-item
            label=""
            prop="tc_name"
          >
            <el-input v-model="courseForm.tc_name" />
          </el-form-item>
          <el-form-item
            label="ID"
            prop="tc_showid"
          >
            <el-input v-model="courseForm.tc_showid" />
          </el-form-item>
          <el-form-item
            label=""
            prop="tc_teacher"
            v-if="courseForm.tc_teacher"
          >
            <el-select
              v-model="courseForm.tc_teacher.u_id"
              placeholder=""
            >
              <el-option
                v-for="item in teacheroptions"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
          </el-form-item>
          <el-form-item
            label=""
            prop="tc_score"
          >
            <el-input
              type="number"
              min="0"
              v-model="courseForm.tc_score"
            />
          </el-form-item>
          <el-form-item
            class="texts"
            label=""
          >
            <editor
              id="editor"
              :catch-data="catchData"
              :content="courseForm.tc_describe"
            />
          </el-form-item>
          <el-form-item label="">
            <div class="form2">
              <el-form-item label="(%)">
                <el-input v-model="courseForm.tc_usual_performance" />
              </el-form-item>
              <el-form-item label="(%)">
                <el-input v-model="courseForm.tc_report_results" />
              </el-form-item>
              <el-form-item label="(%)">
                <el-input v-model="courseForm.tc_experimental_results" />
              </el-form-item>
              <el-form-item label="(%)">
                <el-input v-model="courseForm.tc_training_results" />
              </el-form-item>
              <el-form-item label="(%)">
                <el-input v-model="courseForm.tc_interim_results" />
              </el-form-item>
              <el-form-item label="(%)">
                <el-input v-model="courseForm.tc_extracurricular_performance" />
              </el-form-item>
              <el-form-item label="(%)">
                <el-input v-model="courseForm.tc_final_exam" />
              </el-form-item>
              <el-tag type="warning">
                
              </el-tag>
            </div>
          </el-form-item>
          <el-form-item>
            <div class="formbtn">
              <el-button @click="submitForm('courseFormref')">
                
              </el-button>
              <el-button @click="closeForm()">
                
              </el-button>
            </div>
          </el-form-item>
        </el-form>
      </el-tab-pane>
      <el-tab-pane
        name="coures"
        label=""
      >
        <div>
          <el-table
            :data="courselist"
            stripe
            style="width: 100%"
          >
            <el-table-column
              prop="tc_name"
              label=""
            />
            <el-table-column
              prop="tc_showid"
              label="ID"
            />
            <el-table-column
              prop="tc_score"
              label=""
            />
            <el-table-column label="" v-if="role != 3">
              <div slot-scope="scope">
                <el-button
                  type="danger"
                  size="mini"
                  icon="el-icon-delete"
                  @click="delcourse(scope.row)"
                />
                <el-button
                  type="warning"
                  size="mini"
                  icon="el-icon-edit"
                  @click="alter(scope.row)"
                />
                <el-button
                  type="primary"
                  size="mini"
                  icon="el-icon-share"
                  @click="setknowledgepoint(scope.row)"
                />
              </div>
            </el-table-column>
          </el-table>
        </div>
      </el-tab-pane>
      <el-tab-pane
        name="help"
        label=""
      >
        <h3></h3>
        <p></p>
        <h3></h3>
        <p>\</p>
      </el-tab-pane>
    </el-tabs>
    <!--  -->
    <el-dialog
      title=""
      center
      :visible.sync="dialogVisible1"
      :before-close="handleClose1"
      width="80%"
    >
      <div class="addkpbtn">
        <el-dropdown
          trigger="click"
          @command="handleCommand"
        >
          <el-button type="primary">
            
            <i class="el-icon-arrow-down el-icon--right" />
          </el-button>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item
              v-for="item in kpList"
              :key="item.name"
              :command="item"
            >
              {{ item.name }}
            </el-dropdown-item>
            <el-dropdown-item command="add">
              
            </el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
      <div
        v-if="kpList.length == 0"
        style="text-align:center;font-size:18px;"
      >
        
      </div>
      <div
        class="card1"
        v-for="item in kpList"
        :key="item.name"
      >
        <span>{{ item.name }}</span>
        <el-collapse
          v-if="item.num > 0"
          @change="handleChange"
        >
          <el-collapse-item
            v-for="item1 in item.pointsList"
            :key="item1.tkp_id"
          >
            <template
              slot="title"
              class="card-style"
            >
              <i class="iconfont icon-jiahao" />
              <div class="kPitem">
                <span>{{ item1.tkp_title }}</span>
                <div class="itembtn">
                  <el-button
                    type="warning"
                    size="mini"
                    icon="el-icon-edit"
                    @click.stop="editkp(item1)"
                  >
                    
                  </el-button>
                  <el-button
                    type="danger"
                    size="mini"
                    icon="el-icon-delete"
                    @click.stop="deltkp(item1.tkp_id)"
                  >
                    
                  </el-button>
                </div>
              </div>
            </template>
            <el-card class="kPbody">
              <p></p>
              <div class="tableheader">
                
              </div>
              <div>
                <div
                  v-if="item1.defaultKnowledgeMajors !== undefined && item1.defaultKnowledgeMajors.length === 0"
                  class="tableitem"
                  style="text-align:center;"
                >
                  
                </div>
                <div
                  v-for="(item2, i2) in item1.defaultKnowledgeMajors"
                  :key="i2"
                >
                  <div>
                    <div class="tabletitle">
                      {{ item2.major_graduation_requirements.tmgr_tile }}:{{
                        item2.major_graduation_requirements.tmgr_describe
                      }}
                    </div>
                  </div>
                  <div
                    v-for="item3 in item2.association_Graduation_Requirements"
                    :key="item3.tagr_id"
                  >
                    <div
                      class="tableitem"
                      v-if="item3.checkBox"
                    >
                      {{ item3.tagr_title }}{{ item3.tagr_describe }}
                    </div>
                  </div>
                </div>
              </div>
            </el-card>
          </el-collapse-item>
        </el-collapse>
      </div>

      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="dialogVisible1 = false"> </el-button>
      </span>
    </el-dialog>
    <!-- / -->
    <el-dialog
      center
      :visible.sync="dialogVisible2"
      :before-close="handleClose2"
      width="80%"
    >
      <span slot="title">{{ addkptitel }}</span>
      <div class="addkp">
        <el-form
          label-width="80px"
          ref="addkpFormref"
          :model="kpeditlist"
          :rules="rules"
        >
          <el-form-item
            label=""
            prop="tkp_title"
          >
            <el-input v-model="kpeditlist.tkp_title" />
          </el-form-item>
          <el-form-item label="">
            <el-input
              type="textarea"
              v-model="kpeditlist.tkp_describe"
            />
          </el-form-item>
          <el-form-item v-if="kpeditlist.defaultKnowledgeMajors">
            <p class="addkptitle">
              
            </p>

            <div
              v-for="(item, i) in kpeditlist.defaultKnowledgeMajors"
              :key="i"
            >
              <div class="tabletitle">
                {{ item.major_graduation_requirements.tmgr_tile }}:{{
                  item.major_graduation_requirements.tmgr_describe
                }}
              </div>
              <div
                class="tableitem"
                v-for="item1 in item.association_Graduation_Requirements"
                :key="item1.tagr_id"
              >
                <el-checkbox v-model="item1.checkBox">
                  {{ item1.tagr_title }}{{ item1.tagr_describe }}
                </el-checkbox>
              </div>
            </div>
          </el-form-item>
        </el-form>
      </div>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="addkp('addkpFormref')"></el-button>
        <el-button @click="dialogVisible2 = false"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import editor from './component/editor'
export default {
  data () {
    var validatetc_showid = async (rule, val, callback) => {
      const { data: res } = await this.$http.post('course/sameId', { tc_showid: val })
      if (res.obj && !this.isalter) {
        return callback(new Error('ID'))
      }
    }
    return {
      dialogVisible1: false,
      dialogVisible2: false,
      role: JSON.parse(window.sessionStorage.user).role.role_Id,
      addCourselabel: '',
      addkptitel: '',
      addkpform: {},
      isalter: false,
      activename: 'status',
      kpList: [],
      tc_id:null,
      kpeditlist: [],
      tc_describe: {},
      courseForm: {
        tc_teacher: { u_id: null },
        tc_usual_performance: 0,
        tc_experimental_results: 0,
        tc_interim_results: 0,
        tc_final_exam: 100,
        tc_report_results: 0,
        tc_training_results: 0,
        tc_extracurricular_performance: 0
      },
      courselist: [],
      currentPage: 1,
      pageSize: 10,
      total: 0,
      teacheroptions: [],
      statusTabledata: [
        { name: '', type: '', status: '', tips: '' },
        { name: '', type: '', status: '', tips: '	' },
        { name: '', type: '', status: '', tips: '	' }
      ],
      rules: {
        tc_name: [{ required: true, message: '', trigger: 'blur' }],
        tc_showid: [
          { required: true, message: 'ID', trigger: 'blur' },
          { validator: validatetc_showid, trigger: 'blur' }
        ],
        tc_teacher: [{ required: true, message: '', trigger: 'blur' }],
        tc_score: [{ required: true, message: '', trigger: 'blur' }],
        tkp_title: [{ required: true, message: '', trigger: 'blur' }]
      }
    }
  },
  created () {
    this.getcourselist()
    this.getteacherlist()
  },
  mounted () {},
  methods: {
    handleClick (tab, e) {
      this.$emit('getpath1', tab.label || this.addCourselabel)
      if (this.activename != 'addCourse') {
        this.addCourselabel = ''
        this.isalter = false
      }
      this.initCourseForm()
    },
    async getcourselist () {
      const { data: res } = await this.$http.get('course/showAll')
      this.courselist = res.obj
    },
    // 
    async getteacherlist () {
      const { data: res } = await this.$http.get('admin/showTeacher')
      res.obj.some(item => {
        const obj = { label: item.u_name, value: item.u_id }
        this.teacheroptions.push(obj)
      })
    },

    // 
    async setknowledgepoint (row) {
      this.dialogVisible1 = true
      this.tc_id = row.tc_id
      const { data: res } = await this.$http.post('kpc/cid', { tc_id:this.tc_id })
      this.kpList = res.obj
      console.log(res.obj)
    },
    // 
    alter (row) {
      this.addCourselabel = ''
      this.$emit('getpath1', this.addCourselabel)
      this.activename = 'addCourse'
      this.isalter = true
      this.courseForm = row
      console.log(row)
    },
    // 
    async delcourse (row) {
      const tc_id = row.tc_id
      // 
      const confirmres = await this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      if (confirmres != 'confirm') {
        return this.$message.info('')
      }
      const { data: res } = await this.$http.post('course/del', { tc_id })
      console.log(res)
      this.getcourselist()
    },
    // 
    closeForm () {
      this.addCourselabel = ''
      this.$emit('getpath1', '')
      window.scrollTo(0, 0)
      this.activename = 'coures'
      this.isalter = false
      this.initCourseForm()
    },
    // /
    async submitForm (ref) {
      this.$refs[ref].validate(async isvalidate => {
        if (!isvalidate) return false
        if (!this.isalter) {
          const t_id = this.courseForm.tc_teacher.u_id
          this.courseForm.u_id = t_id
          this.courseForm.tc_describe = this.tc_describe.val
          delete this.courseForm.tc_teacher
          console.log(this.courseForm)
          const { data: res } = await this.$http.post('course/add', this.courseForm)
          console.log(res)
        } else {
          const courseFormjson = JSON.stringify(this.courseForm)
          const { data: res } = await this.$http.post('course/update', courseFormjson)
          console.log(res)
        }
        this.closeForm()
        this.getcourselist()
      })
    },
    // 
    catchData (val) {
      this.courseForm.tc_describe = val
    },
    // 
    editkp (row) {
      this.dialogVisible2 = true
      this.addkptitel = ''
      this.kpIsedit = true
      this.kpeditlist = row
    },
    // 
    async deltkp (tkp_id) {
      // 
      const confirmres = await this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      if (confirmres != 'confirm') {
        return this.$message.info('')
      }
      const { data: res } = await this.$http.get('kpc/del', { params: { tkp_id } })
      this.getcourselist()
    },
    handleCommand (command) {
      // 
      if (command === 'add') {
        console.log(this.kpList)
        const part = this.kpList.length + 1
        const obj = {
          name: `${part}`,
          num: 0,
          pointsList: [
            {
              tkp_part: part,
              table_course_tc_id: {
                tc_id:this.tc_id
              }
            }
          ]
        }
        this.kpList.push(obj)
      } else {
        // 
        this.addkptitel = ''
        this.dialogVisible2 = true
        this.$http.get('kpc/model').then(res => {
          this.kpeditlist = res.body.obj
          this.kpeditlist.tkp_part = command.pointsList[0].tkp_part
          this.kpeditlist.table_course_tc_id = { tc_id: command.pointsList[0].table_course_tc_id.tc_id }
        })
      }
    },
    // /
    async addkp (ref) {
      this.$refs[ref].validate(async isvalidate => {
        if (!isvalidate) return false
        const kpeditlistjson = JSON.stringify(this.kpeditlist)
        if (this.addkptitel === '') {
          const { data: res } = await this.$http.post('kpc/add', kpeditlistjson)
        } else {
          const kplistjson = JSON.stringify(this.kpList)
          const { data: res } = await this.$http.post('kpc/update', kplistjson)
          console.log(res)
        }
        this.handleClose2()
      })
    },
    // 
    initCourseForm () {
      // 
      this.courseForm = {
        tc_teacher: { u_id: null },
        tc_describe: '',
        tc_usual_performance: 0,
        tc_experimental_results: 0,
        tc_interim_results: 0,
        tc_final_exam: 100,
        tc_report_results: 0,
        tc_training_results: 0,
        tc_extracurricular_performance: 0
      }
    },
    handleClose1 () {
      this.dialogVisible1 = false
    },
    handleClose2 () {
      this.dialogVisible2 = false
      this.addkptitel = ''
    },
    handleChange () {
      setTimeout(() => {
        const right = document.getElementsByClassName('el-collapse-item__arrow el-icon-arrow-right')
        right.forEach(element => {
          if (element.className === 'el-collapse-item__arrow el-icon-arrow-right is-active') {
            element.previousSibling.previousSibling.className = 'iconfont icon-jianhao'
          } else {
            element.previousSibling.previousSibling.className = 'iconfont icon-jiahao'
          }
        })
      }, 50)
    }
  },
  components: {
    editor
  }
}
</script>
<style lang="less" scoped>
.el-form {
  max-width: 1179px;
  padding: 0 120px;
  > .el-form-item:nth-child(-n + 4) {
    margin-right: 175px;
  }
}
.form2 {
  width: 839px;
  border: 1px solid #e1e1e1;
  padding: 20px;
  .el-form-item {
    margin-right: 105px;
  }
}
.el-form > .el-form-item:last-child {
  width: 100%;
  text-align: center;
}
.formbtn {
  width: 250px;
  display: flex;
  justify-content: space-between;
}
#editor {
  width: 839px;
  position: relative;
  z-index: 0;
}
.el-select {
  width: 202px;
}
.card1 {
  display: flex;
  justify-content: space-between;
  span {
    line-height: 28px;
  }
}
.el-collapse {
  border: none;
  width: 85%;
}
.kPitem {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.kPbody {
  p {
    padding-left: 5px;
    border-left: 2px solid #337ab7;
  }
}
.tableheader {
  border: 1px solid #e1e1e1;
  border-radius: 4px 4px 0 0;
  padding: 8px;
}
.el-card {
  margin-top: 10px;
}
.el-collapse-item {
  margin-bottom: 10px;
}
.tableitem,
.tabletitle {
  border: 1px solid #e1e1e1;
  padding: 10px;
}
.tabletitle {
  background-color: #e1e1e1;
}
.addkpbtn {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 20px;
}
.el-dropdown-menu {
  width: 130px;
}
.addkptitle {
  height: 40px;
  text-align: left;
  ~ div {
    text-align: left;
  }
}
</style>
