<template>
  <div id="wangeditor">
    <div
      ref="editorElem"
      style="text-align:left"
    />
  </div>
</template>
<script>
import E from 'wangeditor'
export default {
  name: 'EditorElem',
  data () {
    return {
      editor: null
    }
  },
  props: ['catchData', 'content'], // 
  watch: {
    content () {
      this.editor.txt.html(this.content)
    }
  },

  mounted () {
    this.editor = new E(this.$refs.editorElem)
    this.editor.customConfig.onblur = html => {
        this.catchData(html) // htmlcatchData
    }
    this.editor.customConfig.uploadImgServer = ''
    this.editor.customConfig.uploadFileName = ''
    this.editor.customConfig.menus = [
      // 
      'head', // 
      'bold', // 
      'fontSize', // 
      'fontName', // 
      'italic', // 
      'underline', // 
      'strikeThrough', // 
      'foreColor', // 
      'backColor', // 
      'link', // 
      'list', // 
      'justify', // 
      'quote', // 
      'emoticon', // 
      'image', // 
      'table', // 
      'code', // 
      'undo', // 
      'redo' // 
    ]
    // 
    this.editor.customConfig.uploadImgHooks = {
      before: function (xhr, editor, files) {
        // 
        // xhr  XMLHttpRequst editor files 
        //  {prevent: true, msg: 'xxxx'} 
        // return {
        //     prevent: true,
        //     msg: ''
        // }
      },
      success: function (xhr, editor, result) {
        // 
        // xhr  XMLHttpRequst editor result 
        this.imgUrl = Object.values(result.data).toString()
      },
      fail: function (xhr, editor, result) {
        // 
        // xhr  XMLHttpRequst editor result 
      },
      error: function (xhr, editor) {
        // 
        // xhr  XMLHttpRequst editor 
      },
      timeout: function (xhr, editor) {
        // 
        // xhr  XMLHttpRequst editor 
      },

      //  {errno:0, data: [...]} 
      //  JSON 
      customInsert: function (insertImg, result, editor) {
        // 
        // insertImg editor result 

        //  {url:'....'} 
        const url = Object.values(result.data) // result.data
        JSON.stringify(url) // JSON
        insertImg(url)
        // result  JSON 
      }
    }

    this.editor.create() // 
    // if (!this.content) {
    //   this.editor.txt.html('1')
    // }
  }
}
</script>
<style lang="less" scoped>
#wangeditor {
  width: 50rem;
}
</style>
