<template>
  <div>
    <el-tabs
      v-model="activename"
      @tab-click="handleClick"
      tab-position="left"
    >
      <el-tab-pane
        name="status"
        label=""
      >
        <p></p>
        <p></p>
        <p>12</p>
        <el-table
          :data="statusTabledata"
          border
          style="width: 100%"
        >
          <el-table-column
            prop="name"
            width="250"
            label=""
          >
            <div slot-scope="scope">
              <router-link to="">
                {{ scope.row.name }}
              </router-link>
            </div>
          </el-table-column>

          <el-table-column
            width="180"
            prop="type"
            label=""
          />
          <el-table-column
            width="180"
            prop="status"
            label=""
          />
          <el-table-column
            prop="tips"
            label=""
          />
        </el-table>
      </el-tab-pane>

      <el-tab-pane
        name="association"
        label=""
      >
        <div style="padding: 0 20px;">
          <h3></h3>
          <p>
            
            
          </p>
          <el-collapse @change="handleChange">
            <el-collapse-item
              class="card1"
              v-for="item in agrList"
              :key="item.name"
            >
              <template slot="title">
                <i class="iconfont icon-jiahao" /> {{ item.name }}
              </template>
              <div class="mtb20">
                {{ item.desc }}
              </div>
              <div></div>
              <el-table
                :data="item.table"
                border
                style="width: 100%"
              >
                <el-table-column
                  prop="name"
                  width="250"
                  label=""
                />
                <el-table-column
                  prop="desc"
                  label=""
                />
              </el-table>
            </el-collapse-item>
          </el-collapse>
        </div>
      </el-tab-pane>

      <el-tab-pane
        name="speciality"
        label=""
      >
        <div style="padding: 0 20px;">
          <p> </p>
          <el-collapse
            @change="handleChange"
            v-for="(item, i) in specialityList"
            :key="i"
          >
            <el-collapse-item class="card1">
              <template slot="title">
                <i class="iconfont icon-jiahao" />{{ item.tmgr_tile }}
              </template>
              <div class="mtb20">
                {{ item.tmgr_describe }}
              </div>

              <el-collapse
                @change="handleChange"
                v-for="(item1, y) in item.partsList"
                :key="y"
              >
                <el-collapse-item class="card1">
                  <template slot="title">
                    <i class="iconfont icon-jiahao" />{{ i + 1 + '.' + item1.num }}
                  </template>
                  <div class="mtb20">
                    {{ item1.descript }}
                  </div>
                  <el-table
                    :data="item1.pointsList"
                    border
                    style="width: 100%"
                  >
                    <el-table-column
                      prop="table_course_tc_id.tc_name"
                      label=""
                    />
                    <el-table-column
                      prop="tip_weight_level"
                      label=""
                    />
                    <el-table-column
                      prop="tip_weight"
                      label="(%)"
                    />
                  </el-table>
                </el-collapse-item>
              </el-collapse>

              <!-- <div></div> -->
              <el-table
                :data="item.major_index_point"
                border
                style="width: 100%"
              >
                <el-table-column
                  prop="tagr_title"
                  width="250"
                  label=""
                />
                <el-table-column
                  prop="tagr_describe"
                  label=""
                />
              </el-table>
            </el-collapse-item>
          </el-collapse>
        </div>
      </el-tab-pane>
      <!--  -->
      <el-tab-pane
        nmae="assessment"
        label=""
        v-if="role !== 3"
      >
        <el-tabs
          v-model="activeName1"
          type="card"
          @tab-click="handleClick"
        >
          <el-tab-pane
            label=""
            name="first"
          >
            <el-collapse @change="handleChange">
              <el-collapse-item
                class="card1"
                v-for="item in testList"
                :key="item.course.tc_id"
              >
                <template slot="title">
                  <i class="iconfont icon-jiahao" /> {{ item.course.tc_name }}
                </template>
                <div class="addtestbtn">
                  <el-button
                    type="primary"
                    @click="addtest(item)"
                  >
                    
                  </el-button>
                </div>
                <el-table
                  :data="item.testList"
                  border
                  style="width: 100%"
                >
                  <el-table-column
                    prop="tt_id"
                    label="ID"
                  />
                  <el-table-column
                    prop="tt_name"
                    label=""
                  />
                  <el-table-column
                    prop="tt_reach"
                    label=""
                  />
                  <el-table-column label="">
                    <template slot-scope="scope">
                      <el-button
                        type="warning"
                        size="mini"
                        icon="el-icon-edit"
                        @click.stop="edittest(scope.row)"
                      >
                        
                      </el-button>
                      <el-button
                        type="danger"
                        size="mini"
                        icon="el-icon-delete"
                        @click.stop="deltest(scope.row)"
                      >
                        
                      </el-button>
                    </template>
                  </el-table-column>
                </el-table>
              </el-collapse-item>
            </el-collapse>
          </el-tab-pane>
          <el-tab-pane
            label=""
            name="second"
          >
            <el-collapse @change="handleChange">
              <el-collapse-item
                class="card1"
                v-for="item in courselist"
                :key="item.course.tc_id"
              >
                <template slot="title">
                  <i class="iconfont icon-jiahao" /> {{ item.course.tc_name }}
                </template>
                <el-upload
                  class="upload-demo"
                  name="excelTermFile"
                  :on-success="success"
                  action="http://localhost:10088/uploadTermFile"
                  :data="u_id"
                >
                  <el-button
                    type="success"
                    size="mini"
                    icon="el-icon-upload2"
                  >
                    
                  </el-button>
                </el-upload>
                <el-table
                  :data="item.stu_testList"
                  border
                  style="width: 100%"
                >
                  <el-table-column
                    prop="tst_test_name"
                    label=""
                  />
                  <el-table-column
                    prop="tst_term"
                    label=""
                  />
                  <el-table-column
                    prop="tst_test_type"
                    label=""
                  />
                  <el-table-column
                    prop="tst_test_second_test"
                    label=""
                  />
                  <el-table-column
                    prop="tst_teacher.u_name"
                    label=""
                  />
                  <el-table-column
                    prop="tst_upload"
                    label=""
                  />
                </el-table>
              </el-collapse-item>
            </el-collapse>
          </el-tab-pane>
        </el-tabs>
      </el-tab-pane>
      <el-tab-pane
        name="analysis"
        label=""
        v-if="role !== 3"
      >
        <el-table
          :data="JudgmentTestlist"
          stripe
          style="width: 100%"
        >
          <el-table-column
            prop="tt_id"
            label="ID"
          />
          <el-table-column
            prop="tt_name"
            label=""
          />
          <el-table-column
            prop="tt_reach"
            label=""
          />
          <el-table-column label="">
            <template slot-scope="scope">
              <el-button
                type="warning"
                size="mini"
                icon="el-icon-edit"
                @click.stop="editJudgment(scope.row)"
              />
            </template>
          </el-table-column>
        </el-table>
      </el-tab-pane>
    </el-tabs>
    <!--  -->
    <el-dialog
      center
      title=""
      :visible.sync="dialogVisible"
      :before-close="handleClose"
      width="50%"
    >
      <el-form
        :rules="rules"
        ref="JudgmentFormref"
        :model="Judgment"
        class="Judgment"
      >
        <el-form-item label="ID">
          <el-input
            v-model="Judgment.tt_id"
            disabled
          />
        </el-form-item>
        <el-form-item
          label=""
          disabled
        >
          <el-input v-model="Judgment.tt_name" />
        </el-form-item>
        <el-form-item
          label=""
          prop="tt_reach"
        >
          <el-input
            type="number"
            v-model="Judgment.tt_reach"
          />
        </el-form-item>
      </el-form>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="submit('JudgmentFormref')"></el-button>
        <el-button @click="dialogVisible = false"> </el-button>
      </span>
    </el-dialog>
    <!-- / -->
    <el-dialog
      center
      :visible.sync="dialogVisible1"
      :before-close="handleClose1"
      width="50%"
    >
      <template slot="title">
        {{ addDialog }}
      </template>
      <el-form
        :rules="rules"
        ref="addtestFormref"
        :model="addtestForm"
        class="Judgment"
      >
        <el-form-item
          prop="tt_name"
          label=""
        >
          <el-input v-model="addtestForm.tt_name" />
        </el-form-item>
        <el-form-item
          label=""
          prop="tt_reach"
        >
          <el-input
            type="number"
            min="0"
            v-model="addtestForm.tt_reach"
          />
        </el-form-item>
        <el-form-item>
          <el-button
            class="addeqbtn"
            type="success"
            @click="addeq"
          >
            
          </el-button>
        </el-form-item>
        <el-form-item>
          <div
            v-for="item in addtestForm.eqList"
            :key="item.teq_id"
          >
            <div class="tabletitle">
              {{ item.teq_name }}
              <div class="eqbtn">
                <el-button
                  type="warning"
                  @click="altereq(item)"
                >
                  
                </el-button>
                <el-button
                  type="danger"
                  @click="deleq(item)"
                >
                  
                </el-button>
              </div>
            </div>
            <div
              class="tableitem"
              v-for="item1 in item.knowledgePointsList"
              :key="item1.tkp_id"
            >
              <el-checkbox v-model="item1.check">
                {{ item1.tkp_title }}
              </el-checkbox>
            </div>
          </div>
        </el-form-item>
      </el-form>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="submittset('addtestFormref')"></el-button>
        <el-button @click="handleClose1"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      center
      :visible.sync="dialogVisible2"
      :before-close="handleClose2"
      width="50%"
    >
      <template slot="title">
        {{ addeqtitle }}
      </template>
      <el-form
        :rules="rules"
        ref="addeqFormref"
        :model="addeqForm"
      >
        <el-form-item
          prop="teq_name"
          label=""
        >
          <el-input v-model="addeqForm.teq_name" />
        </el-form-item>
        <el-form-item label="">
          <el-input
            type="textarea"
            v-model="addeqForm.teq_describe"
          />
        </el-form-item>
        <el-form-item>
          <div
            class="tableitem"
            v-for="item in addeqForm.knowledgePointsList"
            :key="item.tkp_id"
          >
            <el-checkbox v-model="item.check">
              {{ item.tkp_title }}
            </el-checkbox>
          </div>
        </el-form-item>
      </el-form>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="submieq('addeqFormref')"></el-button>
        <el-button @click="handleClose2"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data () {
    return {
      u_id: { u_id: JSON.parse(window.sessionStorage.user).id },
      role: JSON.parse(window.sessionStorage.user).role.role_Id,
      addtab: '',
      addDialog: '',
      addeqtitle: '',
      addtestForm: {},
      addeqForm: {},
      testitem: {},
      isalter: false,
      iseqalter: false,
      dialogVisible: false,
      dialogVisible1: false,
      dialogVisible2: false,
      courselist: [],
      JudgmentTestlist: [],
      Judgment: {},
      activename: 'status',
      activeName1: 'first',
      testList: [],
      rules: {
        tt_reach: [{ required: true, message: '', trigger: 'blur' }],
        tt_name: [{ required: true, message: '', trigger: 'blur' }],
        teq_name: [{ required: true, message: '', trigger: 'blur' }]
      },
      statusTabledata: [
        { name: '', type: '', status: '', tips: '	' },
        { name: '', type: '', status: '', tips: '	' }
      ],
      agrList: [
        {
          name: '',
          desc: '',
          table: [{ name: '1', desc: '' }]
        },
        {
          name: '',
          desc:
            '',
          table: [
            {
              name: '1',
              desc: ''
            },
            {
              name: '2',
              desc:
                ''
            }
          ]
        },
        {
          name: '/',
          desc:
            '',
          table: [
            {
              name: '3',
              desc:
                ''
            }
          ]
        },
        {
          name: '',
          desc:
            ' ',
          table: [
            {
              name: '4',
              desc:
                ''
            }
          ]
        },
        {
          name: '',
          desc: '',
          table: [
            {
              name: '5',
              desc:
                '	'
            }
          ]
        },
        {
          name: '',
          desc:
            '',
          table: [
            {
              name: '6',
              desc:
                ''
            }
          ]
        },
        {
          name: '',
          desc: '',
          table: [
            {
              name: '7',
              desc: ''
            }
          ]
        },
        {
          name: '',
          desc: '',
          table: [
            {
              name: '8',
              desc: ''
            }
          ]
        },
        {
          name: '',
          desc: '',
          table: [
            {
              name: '9',
              desc: ''
            }
          ]
        },
        {
          name: '',
          desc:
            '',
          table: [
            {
              name: '10',
              desc:
                ''
            }
          ]
        },
        {
          name: '',
          desc: '',
          table: [
            {
              name: '11',
              desc: ''
            }
          ]
        },
        {
          name: '',
          desc: '',
          table: [
            {
              name: '12',
              desc: '	'
            }
          ]
        }
      ],
      teacheroptions: [],
      // 
      specialityList: []
    }
  },
  created () {
    this.getmajoGraduation()
    this.getTestlist()
    this.getTestlist1()
    this.getteacherlist()
    this.getCourselist()
  },
  methods: {
    handleClick (tab, e) {
      this.$emit('getpath2', tab.label || this.addtab)
    },
    // 
    async getteacherlist () {
      const { data: res } = await this.$http.get('admin/showTeacher')
      res.obj.some(item => {
        const obj = { label: item.u_name, value: item.u_id }
        this.teacheroptions.push(obj)
      })
    },
    // 
    async getmajoGraduation () {
      const { data: res } = await this.$http.get('mgr/showAll')
      this.specialityList = res.obj
    },
    // 
    async getTestlist () {
      const { data: res } = await this.$http.get('test/showAll')
      this.testList = res.obj
      console.log(this.testList)
    },
    // 
    async getCourselist () {
      const { data: res } = await this.$http.get('test/showControlTest')
      this.courselist = res.obj
      console.log(this.courselist)
    },
    // 
    async getTestlist1 () {
      const { data: res } = await this.$http.get('test/showAllTest')
      this.JudgmentTestlist = res.obj
    },
    // 
    async addtest (item) {
      this.dialogVisible1 = true
      const { data: res } = await this.$htpp.post()
      const tc_id = item.course.tc_id
      this.addtestForm.tc_id = tc_id
    },
    // 
    async edittest (row) {
      this.dialogVisible1 = true
      this.isalter = true
      this.addDialog = ''
      this.addtestForm = row
    },
    // 
    async deltest (row) {
      const tt_id = row.tt_id
      // 
      const confirmres = await this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      if (confirmres != 'confirm') {
        return this.$message.info('')
      }
      const { data: res } = await this.$http.post('test/del', { tt_id })
      this.getTestlist()
    },
    // 
    async addeq () {
      this.dialogVisible2 = true
      const tt_id = this.addtestForm.tt_id
      const { data: res } = await this.$http.post('test/model', { tt_id })
      this.addeqForm = res.obj.eqList[0]
    },
    // 
    altereq (row) {
      this.addeqtitle = ''
      this.dialogVisible2 = true
      this.iseqalter = true
      this.addeqForm = row
    },
    // 
    async deleq (row) {
      // 
      const confirmres = await this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      if (confirmres != 'confirm') {
        return this.$message.info('')
      }
      this.addtestForm.eqList.some((item, i) => {
        if (item.teq_id === row.teq_id) {
          this.addtestForm.eqList.splice(i, 1)
          return true
        }
      })
    },
    // 
    editJudgment (row) {
      this.dialogVisible = true
      this.Judgment = row
      console.log(row)
    },
    // /
    submittset (ref) {
      this.$refs[ref].validate(async isvalidate => {
        if (!isvalidate) return false
        if (this.isalter) {
          const addtestJson = JSON.stringify(this.addtestForm)
          console.log(addtestJson)
          const { data: res } = await this.$http.post('test/update', addtestJson)
        } else {
          this.addtestForm.u_id = JSON.parse(window.sessionStorage.user).id
          const { data: res } = await this.$http.post('test/add', this.addtestForm)
        }
        this.getTestlist()
        this.handleClose1()
      })
    },
    // /
    submieq (ref) {
      this.$refs[ref].validate(async isvalidate => {
        if (!isvalidate) return false
        if (!this.iseqalter) {
          console.log(this.addtestForm)
          this.addtestForm.eqList.push(this.addeqForm)
        }
        this.handleClose2()
      })
    },
    // 
    submit (ref) {
      this.$refs[ref].validate(async isvalidate => {
        if (!isvalidate) return false
        const { tt_id, tt_reach } = this.Judgment
        const data = { tt_id, tt_reach }
        const { data: res } = await this.$http.post('test/changeReach', data)
        this.dialogVisible = false
        this.getTestlist1()
      })
    },

    // Excel
    success () {
      this.getCourselist()
    },
    handleChange () {
      setTimeout(() => {
        const right = document.getElementsByClassName('el-collapse-item__arrow el-icon-arrow-right')
        right.forEach(element => {
          if (element.className === 'el-collapse-item__arrow el-icon-arrow-right is-active') {
            element.previousSibling.previousSibling.className = 'iconfont icon-jianhao'
          } else {
            element.previousSibling.previousSibling.className = 'iconfont icon-jiahao'
          }
        })
      }, 50)
    },
    handleClose () {
      this.dialogVisible = false
    },
    handleClose1 () {
      this.isalter = false
      this.addtestForm = {}
      this.addDialog = ''
      this.dialogVisible1 = false
    },
    handleClose2 () {
      this.addeqForm = {}
      this.dialogVisible2 = false
      this.iseqalter = false
      this.addeqtitle = ''
    }
  }
}
</script>
<style lang="less" scoped>
.el-collapse,
.el-collapse-item__header {
  border-bottom: none;
}
.el-collapse-item {
  margin-bottom: 10px;
}
a {
  text-decoration: none;
}
.iconfont {
  color: #6091ba;
}
.addtestbtn {
  text-align: right;
  margin-bottom: 10px;
}
.upload-demo {
  text-align: right;
  margin-bottom: 10px;
  height: 28px;
  overflow: hidden;
}
.tableitem,
.tabletitle {
  border: 1px solid #e1e1e1;
  padding: 10px;
}
.tabletitle {
  background-color: #e1e1e1;
}
.eqbtn {
  float: right;
}
.el-form-item {
  overflow: hidden;
}
.addeqbtn {
  float: right;
}
</style>
